.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.into-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.65;
}

.hero {
    height: 100%;
    width: 100%;
}

.hero .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.hero .content h1{
    font-size: 3rem;
    padding: 0.6rem 0 1.5rem;
}

.hero .content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
    padding: 0.6rem 0 1.5rem;
}

.content .btn{
    margin: 1rem 0.2rem;

}

@media screen and (max-width: 1135px){
    
    .hero .content h1{
        font-size: 3rem;  
    }
    
    .hero .content p{
        font-size: 1.4rem;
    }

    .content .btn{
        margin: 1rem 5.5rem;
    }

    .content .btn {
        display: flex;
        flex-direction: row;
    }

}

